<template>
  <v-container>
    <i-label-title>Principais Eventos</i-label-title>
    <v-carousel class="carousel-banners">
      <v-carousel-item :src="test"></v-carousel-item>
      <v-carousel-item src="../../../../assets/img/events/lolla.jpg"></v-carousel-item>
      <v-carousel-item src="../../../../assets/img/events/rev.jpg"></v-carousel-item>
    </v-carousel>
  </v-container>
</template>

<script>
import rockImg from '../../../../../static/img/events/rockinrio.jpg'
export default {
  name: 'p-banners',
  computed: {
    test () {
      return rockImg
    }
  }
}
</script>

<style scoped>
  .carousel-banners {
    height: 350px;
  }
</style>



